.contacts {
  padding: 43px 0 56px 0;
  background: linear-gradient(var(--text-light) 127px, #fff5f6 127px);
  @media screen and (min-width: 768px) {
    padding: 60px 0 79px 0;
    background: linear-gradient(var(--text-light) 100px, #fff5f6 100px);
  }
  @media screen and (min-width: 1280px) {
    padding: 66px 0 83px 0;
    background: linear-gradient(var(--text-light) 168px, #fff5f6 168px);
  }
}
.contacts-card {
  @media screen and (min-width: 768px) {
    display: flex;
  }
}
.contacts-card__item {
  &:not(:last-child) {
    @media screen and (max-width: 767px) {
      margin-bottom: 20px;
    }
    @media screen and (min-width: 768px) {
      margin-right: 20px;
    }
    @media screen and (min-width: 1280px) {
      margin-right: 30px;
    }
  }
  background: var(--text-light);
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
  padding: 43px 21px 43px 20px;
}
.contacts-card__representations {
  display: inline-block;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.34;
  text-align: center;
  letter-spacing: 0.04em;
  color: var(--text-light);
  background: #f0d1a5;
  border-radius: 4px;
  padding: 0 24px;
  margin-bottom: 25px;
  &--color {
    background-color: #c2e297;
  }
}
.contacts-card__sity,
.schedule__list {
  margin-bottom: 25px;
}
.contacts-card__sity-title {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.86;
  letter-spacing: 0.04em;
  margin-bottom: 10px;
}
.contacts-card__sity-text,
.contacts__link {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.5;
  color: var(--second-text-color);
}
.schedule__list,
.contacts__list {
  border-top: 1px solid #e1e1e1;
  padding-top: 25px;
}
.schedule__item:not(:last-child) {
  margin-bottom: 10px;
}
.schedule__text {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.86;
}
.schedule__text-time {
  color: var(--second-title-color);
}
.contacts__buttons-position {
  @media screen and (max-width: 767px) {
    margin-top: 54px;
  }
  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: center;
    margin-top: 81px;
  }
  @media screen and (min-width: 1280px) {
    margin-top: 86px;
  }
}
.contacts__btn {
  display: flex;
  align-items: center;
  height: 44px;
  padding: 0 17px;
  font-weight: bold;
  font-size: 16px;
  line-height: 1.31;
  color: var(--text-light);
  background-color: #d41342;
  border-radius: 22px;
  transition: background-color 250ms var(--main-cubic-bezier);
  &:hover,
  &:focus {
    background-color: #d41349;
  }
  @media screen and (max-width: 767px) {
    margin: 0 auto;
  }
}
.contacts__btn--franchise {
  background-color: var(--text-light);
  color: var(--main-corp-color);
  transition: background-color 250ms var(--main-cubic-bezier);
  @media screen and (max-width: 767px) {
    margin-top: 10px;
  }
  @media screen and (min-width: 768px) {
    margin-left: 15px;
  }
  &:hover,
  &:focus {
    background-color: #ecebeb;
  }
}
.icon-arrow-right {
  width: 14px;
  height: 18px;
  margin-left: 5px;
  fill: currentColor;
}
